import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'Choisir une branche';
  branch = -1;
  inputs = [false, false, false, false];

  constructor() {}

  display(val) {
    switch (val) {
        case 1:
          if (!this.inputs[0]) {
            this.branch = 1;
            this.inputs[0] = true;
          } else {
            this.branch = -1;
            this.inputs[0] = false;
          }
          break;
        case 2:
            if (!this.inputs[1]) {
                this.branch = 2;
                this.inputs[1] = true;
            } else {
                this.branch = -1;
                this.inputs[1] = false;
            }
            break;
        case 3:
            if (!this.inputs[2]) {
                this.branch = 3;
                this.inputs[2] = true;
            } else {
                this.branch = -1;
                this.inputs[2] = false;
            }
            break;
        case 4:
            if (!this.inputs[3]) {
                this.branch = 4;
                this.inputs[3] = true;
            } else {
                this.branch = -1;
                this.inputs[3] = false;
            }
            break;
        default:
          break;
    }
  }
}
